import { useRoute, useRouter } from 'vue-router';
import { ROUTE_NAMES, ROUTE_TO_STEP, STEP_CONFIGS, STEP_TO_ROUTE, TOTAL_STEPS, } from '../router/route-constants';
/**
 * 
 * 
 */
export function useStepNavigation() {
    const router = useRouter();
    const route = useRoute();
    const transitionName = ref('slide-left');
    // 
    const currentStep = computed(() => {
        const step = route.meta?.step;
        if (step)
            return step;
        const routeName = route.name;
        return ROUTE_TO_STEP[routeName] || 1;
    });
    // 
    const canGoPrevious = computed(() => currentStep.value > 1);
    // 
    const isLastStep = computed(() => currentStep.value === TOTAL_STEPS);
    // 
    const goToStep = (step) => {
        if (step >= 1 && step <= TOTAL_STEPS) {
            const routeName = STEP_TO_ROUTE[step];
            if (routeName) {
                router.push({ name: routeName });
            }
        }
    };
    // 
    const goToPrevious = () => {
        const prevStep = currentStep.value - 1;
        if (prevStep >= 1) {
            goToStep(prevStep);
        }
    };
    // 
    const goToNext = () => {
        const nextStep = currentStep.value + 1;
        if (nextStep <= TOTAL_STEPS) {
            goToStep(nextStep);
        }
    };
    // 
    const goToFirst = () => {
        router.push({ name: ROUTE_NAMES.BASIC_INFO });
    };
    // 
    watch(() => route.name, (newRoute, oldRoute) => {
        const newStep = ROUTE_TO_STEP[newRoute] || 1;
        const oldStep = ROUTE_TO_STEP[oldRoute] || 1;
        // 
        transitionName.value = newStep > oldStep ? 'slide-left' : 'slide-right';
    });
    return {
        currentStep,
        totalSteps: TOTAL_STEPS,
        stepConfigs: STEP_CONFIGS,
        canGoPrevious,
        isLastStep,
        transitionName,
        goToPrevious,
        goToNext,
        goToStep,
        goToFirst,
    };
}
