/**
 *  Composable
 */
import { useCharacterStore } from '../store';
import { findMatchingPreset, hasPresets } from '../utils/preset-manager';
/**
 * 
 */
export function usePresetModal() {
    const characterStore = useCharacterStore();
    const showModal = ref(false);
    const modalMode = ref('manage');
    const openManageModal = () => {
        modalMode.value = 'manage';
        showModal.value = true;
    };
    const openLoadModal = () => {
        modalMode.value = 'load';
        showModal.value = true;
    };
    const closeModal = () => {
        showModal.value = false;
    };
    const checkAndShowLoadModal = () => {
        if (hasPresets()) {
            openLoadModal();
        }
    };
    const checkMatchingPreset = () => {
        return findMatchingPreset(characterStore);
    };
    return {
        showModal,
        modalMode,
        openManageModal,
        openLoadModal,
        closeModal,
        checkAndShowLoadModal,
        checkMatchingPreset,
    };
}
