/**
 *  composable
 *  DRY  ItemList  DestinedOneList 
 */
/**
 * 
 * @param selectedItems  getter 
 * @param availablePoints  getter 
 */
export function useSelectableList(selectedItems, availablePoints) {
    /**
     * 
     *  lodash  _.some 
     */
    const isSelected = (item) => {
        return _.some(selectedItems(), { name: item.name });
    };
    /**
     * 
     */
    const canSelect = (item) => {
        return availablePoints() >= item.cost;
    };
    /**
     * 
     * 
     */
    const isDisabled = (item) => {
        if (isSelected(item))
            return false;
        return !canSelect(item);
    };
    return {
        isSelected,
        canSelect,
        isDisabled,
    };
}
/**
 * 
 *  VueUse  useSet 
 */
export function useExpandableCards() {
    const expandedCards = ref(new Set());
    /**
     * 
     */
    const toggleExpand = (name, event) => {
        event?.stopPropagation();
        if (expandedCards.value.has(name)) {
            expandedCards.value.delete(name);
        }
        else {
            expandedCards.value.add(name);
        }
    };
    /**
     * 
     */
    const isExpanded = (name) => {
        return expandedCards.value.has(name);
    };
    /**
     * 
     */
    const expandAll = (names) => {
        names.forEach(name => expandedCards.value.add(name));
    };
    /**
     * 
     */
    const collapseAll = () => {
        expandedCards.value.clear();
    };
    return {
        expandedCards,
        toggleExpand,
        isExpanded,
        expandAll,
        collapseAll,
    };
}
