import type { Rarity } from '../types';

/**
 * /
 */
export const RARITY_OPTIONS: { value: Rarity; label: string; color: string }[] = [
  { value: 'common', label: '', color: '#9e9e9e' },
  { value: 'uncommon', label: '', color: '#4caf50' },
  { value: 'rare', label: '', color: '#2196f3' },
  { value: 'epic', label: '', color: '#9c27b0' },
  { value: 'legendary', label: '', color: '#ff9800' },
  { value: 'mythic', label: '', color: '#e91e63' },
  { value: 'only', label: '', color: '#ff0000' },
];

/**
 * 
 */
export const EQUIPMENT_TYPE_OPTIONS = [
  { label: '', value: '' },
  { label: '', value: '' },
  { label: '', value: '' },
] as const;

/**
 * 
 */
export const SKILL_TYPE_OPTIONS = [
  { label: '', value: '' },
  { label: '', value: '' },
] as const;

/**
 * 
 */
export const CATEGORY_OPTIONS = [
  { value: 'equipment', label: '' },
  { value: 'item', label: '' },
  { value: 'skill', label: '' },
] as const;

/**
 * 
 */
export const getRarityLabel = (value: string | undefined): string => {
  return RARITY_OPTIONS.find(opt => opt.value === value)?.label || '';
};

/**
 * 
 */
export const getRarityColor = (value: string | undefined): string => {
  return RARITY_OPTIONS.find(opt => opt.value === value)?.color || '#9e9e9e';
};
